import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {getCheckoutMnpProps, getMnpContactMethods} from "../../selectors";
import {changeCustomerMnpDataFormField, changeBusinessMnpAddressFormField} from "../../actions/app";
import {requestCartMnpData, saveDefaultCartData} from "../../actions/data";
import MulticartMnpSectionComponent from "./MulticartMnpSectionComponent";
import {Expander, Section, Toggle} from "eshop/modules/core/components/ui/Expander";
import MulticartValidationComponent from "../MulticartValidationComponent";

class MulticartMnpFormView extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.requestCartMnpData({
            migrationMode: this.getDefaultMigrationMode(),
            contactMethod: this.props.getMnpContactMethods(this.props.mnpFormData.contactMethods)[0].value,
            isHeadquartersAddressSame: true,
            identifier: 'NIP',
            agreementType: '1'
        });
    }

    getDefaultMigrationMode () {
        let sorted = this.props.mnpFormData.migrationModes.sort((a, b) => a.priority - b.priority);
        return sorted.filter(a => a.value == 'EOP') ? sorted.filter(a => a.value == 'EOP')[0].value : sorted[0].value;
    }

    render() {
        return (
                <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-no-padding-l u-spacing-l u-padding-top">
                    <h2>{this.props.descriptions.title || this.props.title}</h2>
                    <MulticartValidationComponent messageType="mnpData"/>
                { this.props.mnpData instanceof Array && this.props.mnpData.map((entry, idx) => (
                    <div key={idx+"_view"}>
                        <MulticartMnpSectionComponent {...this.props}
                                                      entry={entry}
                                                      entryIndex={idx}
                                                      key={idx+"_section"} />
                    </div> ))
                }
            </div>
        )
    }
}

MulticartMnpFormView.defaultProps = {
    title: "Dane do przeniesienia numeru"
}

const mapStateToProps = state => ({
    ...getCheckoutMnpProps(state),
    getMnpContactMethods: contactMethods => getMnpContactMethods(contactMethods)(state)
});

const actions = {
    saveDefaultCartData,
    changeCustomerMnpDataFormField,
    changeBusinessMnpAddressFormField,
    requestCartMnpData
};

export default connect(mapStateToProps, actions)(MulticartMnpFormView);
